header {
  position: fixed;
  top: 0px;
  width: 100%;
  height: 11.9rem;
  background-color: $purple;
  display: flex;
  .container {
    @include flex-element(row, space-between, center);
    nav {
      @include flex-element(row, space-between, flex-end);
      ul {
        display: flex;
        li {
          margin-right: 5.6rem;
          &:last-child {
            margin-right: 0px;
          }
          a {
            color: $white;
            @include flex-element(row, flex-start, center);
            img {
              margin-right: 1.6rem;
            }
          }
        }
      }
    }
    @include responsive(768px) {
      nav {
        display: none;
      }
    }

    @include responsive(480px) {
      nav {
        display: none;
      }
      .logo {
        width: 16.4rem;
      }
    }
  }
}
